	//var phpList = ["CORR","BACK_ENG_CORR","BACK_FRA_CORR","ENG_BLEU","FRA_BLEU","ENG_METEOR","FRA_METEOR","LENGTH_DIFF","TOKEN_LENGTH_DIFF","SENTENCE_LEN_DIFF"];
	var phpList = fieldList;
	const filter_selected_id = useSelector(state => state.lineup_filter)
	const lineup_filter_state = useSelector(state => state.filter)
	const lineup_to_php_state = useSelector(state => state.lineup_to_php)
	const php_lineup_state = useSelector(state => state.php_lineup)

	useEffect(() => {
		//   console.log(filter_selected_id)
		if (lineup_filter_state != null) {
			if (filter_selected_id.rev_filter_id.length != 0) {
				let filtered_data = [];
				filter_selected_id.rev_filter_id.forEach(filter_id => {
					const filterInfo = lineup_filter_state.rev_filter_info.filter(d => d.id === filter_id)[0]

					filterInfo.filter_id.forEach(id => {
						data.forEach(d => {
							if (d['___id'] == id)
								filtered_data.push({ data: d, color: filterInfo.rev_color });
							//filtered_data.push({data : d, color: filterInfo.rev_color});
						})
					})

				})

				pcp_hl(filtered_data, 'line_filter_highlight', true)
			}
			else
				pcp_hl([], 'line_filter_highlight', true)
		}
	}, [filter_selected_id])

	useEffect(() => {
		// console.log(lineup_to_php_state.mouseover_id)
		let mouseover_data = null;
		data.forEach(d => {
			if (d['___id'] == lineup_to_php_state.mouseover_id)
				mouseover_data = d;
		})
		tableHover(mouseover_data);
	}, [lineup_to_php_state])

