import React, { useRef } from 'react';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import Title from './Title'
import MyLineup from './MyLineup'
import FilterHeatmap from './FilterHeatmap'
import PCP3 from './PCP3'
import PHP from './PHP'
import BasicInfo from './BasicInfo'
import CanvasScatter from './CanvasScatter'
import TextCompare from './TextCompare'
import FilterBoxPlot from './FilterBoxPlot';
import { globalData, colorScaleMap, fieldList, lang } from './reducers/StaticData'
import FilterBundling from "./FilterBundling"
import ToggleButton from '@mui/material/ToggleButton';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';
import "./css/legend.css"
import { Divider } from '@mui/material';

const Item = styled(Paper)(({ theme }) => ({
	...theme.typography.body2,
	padding: theme.spacing(1),
	textAlign: 'center',
	color: theme.palette.text.secondary,
}));

const metric_explain = {
	"CORR": "Cosine Similarity",
	"BACK_ENG_CORR": "ENG Cosine \n Similarity",
	"BACK_FRA_CORR": 'FRA Cosine \n Similarity',
	"BACK_KOR_CORR": 'KOR Cosine \n Similarity',
	"LENGTH_DIFF": "Length Ratio(R)",
	"TOKEN_LENGTH_DIFF": "Token Length \n Ratio(R)",
	"ENG_BLEU": "ENG BLEU",
	"FRA_BLEU": "FRA BLEU",
	"KOR_BLEU": "KOR BLEU",
	"ENG_METEOR": "ENG METEOR",
	"FRA_METEOR": "FRA METEOR",
	"KOR_METEOR": "KOR METEOR"
}


function Copyright(props) {
	return (
		<Typography variant="body2" color="text.secondary" align="center" {...props}>
			{'Copyright  SNU HCI Lab, Sebeom Park, Soohyun Lee in '}

			{new Date().getFullYear()}
			{'.'}
		</Typography>
	);
}

function MetricLegend() {
	let metric = fieldList.map(d => {
		return (
			<div class="legend-element" style={{ background: colorScaleMap[d](0.5), whiteSpace: 'pre-line' }}>
				<span>{metric_explain[d]}</span>
			</div>
		)
	})
	return (

		<div class="legend-container">
			<span>Metric: </span>
			{metric}
		</div>
	)
}

function getHeader() {
	if (lang === "FRA")
		return (
			<pre style={{ letterSpacing:"0px", whiteSpace: "pre-wrap" }}> VANT     Dataset : Scielo Corpus      Source Language : English - Target Language : French    Total size of data : {globalData.length}</pre>
		)
	else
		return (
			<pre style={{ letterSpacing:"0px", whiteSpace: "pre-wrap" }}> VANT     Dataset : Korean Language Resources for Everyone      Source Language : English - Target Language : Korean    Total size of data : {globalData.length}</pre>
		)
}
function Dashboard2() {
	const handleChange = (event, value) => {
		if(value)
			window.location.href = value
		else
			window.location.href = lang
	};
	var ratio = 0.8;
	// document.body.style.width = '1920px';
	// // document.body.style.height = '1080px';
	// document.body.style.height = '400px';
	// document.body.style.transform = `scale(${ratio},${ratio})`;
	// document.body.style.transformOrigin = `center top`;
	// document.body.style.margin = 'auto'
	return (
		<Box sx={{ flexGrow: 1, width: 1920, height: 500, margin: 'auto', transform: `scale(${ratio},${ratio})`, transformOrigin: `center top` }}>
			<Grid container item spacing={1}>

				{/* Header */}
				<Grid item xs={12}>
					<Item style={{ textAlign: 'left', paddingLeft: '20px', backgroundColor: "#FFFFF0" }}>
						<Title style={{ height:70 }}>
							<div style={{ height: 60 }}>
								<div style={{ width: "90%", float: "left"}}>
									{getHeader()}
								</div>
								<ToggleButtonGroup
									color="primary"
									value={lang}
									exclusive
									onChange={handleChange}
									style={{ fontSize: "1em", float: "right"}}
								>
									<ToggleButton style={{ fontSize: "0.6em", lineHeight: 1 }} value={"FRA"}>FRA</ToggleButton>
									<ToggleButton style={{ fontSize: "0.6em", lineHeight: 1 }} value={"KOR"}>KOR</ToggleButton>
								</ToggleButtonGroup>
							</div>
							<div style={{ marginTop:-10 }} >
								{MetricLegend()}
							</div>

						</Title>

					</Item>
				</Grid>

				<Grid container item spacing={1}>

					<Grid item xs={12} xl={12}>

						<Grid container item spacing={1} >
							<Grid item container xs={10} xl={10} spacing={1}>

								<Grid item xs={12} xl={12}>
									<Item elevation={3}>
										<Title> Distribution View </Title>
										<Grid item container justifyContent="space-between">
											<Grid item xs={3} xl={3}>
												<CanvasScatter />
											</Grid>
											<Divider orientation="vertical" flexItem style={{marginLeft:"-8px"}} />
											<Grid item xs={9} xl={9}>
												<PHP />
											</Grid>
										</Grid>
									</Item>
								</Grid>

								<Grid item xs={3} xl={3}>
									<Item elevation={3}>
										<TextCompare />
									</Item>
								</Grid>
								<Grid item xs={9} xl={9}>
									<Item elevation={3}>
										<MyLineup/>
									</Item>
								</Grid>
							</Grid>
							<Grid item xs={2} xl={2} >
								<Item elevation={3}>
									<Title>Ruleset View </Title>
									<Typography variant="body1">Ruleset Information</Typography>
									<FilterHeatmap />
									<br />
									<br />
									<br />
									<Divider style={{marginTop:-12}}/>
									<br />
									<Typography variant="body1">Ruleset Status</Typography>
									<br />
									<FilterBoxPlot />
									<Divider/>
									<br />
									<Typography variant="body1">Ruleset Relationship</Typography>
									<FilterBundling />
								</Item>
							</Grid>

						</Grid>
					</Grid>



				</Grid>

			</Grid>
			{/* <Copyright sx={{ pt: 4 }} /> */}
		</Box>
	);
}
export default Dashboard2
