import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';

function TextHeatmap() {

    const svg_heatmap = useRef();
    const ngrams = globalData[8279].ngram
    const ngram_eng_original = ngrams.ngram_eng_original
    const ngram_eng_back = ngrams.ngram_eng_back

    const ngram_fra_original = ngrams.ngram_fra_original
    const ngram_fra_back = ngrams.ngram_fra_back

    const width = 600
    const height = 5000
    const box_height = 10
    const box_width = 200

    useEffect(() => {
        let svg = d3.select(svg_heatmap.current).attr("width", width).attr("height", height)
        function draw(id) {

            const new_svg = svg.append("g").attr("transform",(d,i) =>{
                return `translate(0, ${(box_height*2+10) * id})`
            })

            const eng_original = new_svg.append("g")
            const ngrams = globalData[id].ngram
            const ngram_eng_original = ngrams.ngram_eng_original
            const ngram_eng_back = ngrams.ngram_eng_back
        
            const ngram_fra_original = ngrams.ngram_fra_original
            const ngram_fra_back = ngrams.ngram_fra_back

            
            const fill_scale = d3.scaleOrdinal().domain([0, 1, 2, 3, 4]).range(["white", "LightGreen", "LightSeaGreen", "lightskyblue", "lightsalmon"])
            const eng_original_widthScale = d3.scaleLinear().domain([0, globalData[id].ENG_SENTENCE.length]).range([0, box_width])
            
            eng_original.selectAll("rect").data(ngram_eng_original, d => d).join("rect").attr("x", (d, i) => {
                var pos = 0;
                for (let index = 0; index < i; index++) {
                    pos += eng_original_widthScale(ngram_eng_original[index].token.length + 1)
                }
                return pos
            }).attr("y", 0).attr

                ("width", d => eng_original_widthScale(d.token.length)).attr("height", box_height).attr("fill", d => fill_scale(d.n_gram))


            const eng_back = new_svg.append("g").attr("transform", (d, i) => {
                return `translate(0, ${box_height})`
            })

            const eng_back_widthScale = d3.scaleLinear().domain([0, globalData[id].BACK_ENG_SENTENCE.length]).range([0, box_width])

            eng_back.selectAll("rect").data(ngram_eng_back, d => d).join("rect").attr("x", (d, i) => {
                var pos = 0;
                for (let index = 0; index < i; index++) {
                    pos += eng_back_widthScale(ngram_eng_back[index].token.length + 1)
                }
                return pos
            }).attr("y", 0).attr("width", d => eng_back_widthScale(d.token.length)).attr("height", box_height).attr("fill", d => fill_scale(d.n_gram))



        }

        for(let i =0;i<300;i++)
            draw(i)

    }, [])

    return (
        <div>
            <svg ref={svg_heatmap}>

            </svg>
        </div>
    )
}

export default TextHeatmap