  export const PHP_LINEUP_FILTER = "SET_PHP_LINEUP_FILTER"
    export const COLUMN_ORDER = "SET_COLUMN_ORDER"

 
  export const SET_PHP_LINEUP_FILTER = data => ({ type: PHP_LINEUP_FILTER, data })
  export const save_column_order = data => ({ type: COLUMN_ORDER, data })

  
  
  const php_linup = (state = initalState, action) => {
    switch (action.type) {
      case "SET_PHP_LINEUP_FILTER":
        return {
          ...state,
          php_filter_map: action.data
        }
      case "SET_COLUMN_ORDER":
        return {
          ...state,
          column_order : action.data
        }
  
      // default    state count undefined  ! default 
      default:
        return state;
    }
  };
  
  export default php_linup