import './App.css';
import { BrowserRouter, Route, Switch, Link } from "react-router-dom"
import Dashboard from './Dashboard'
import FilterHeatmap from "./FilterHeatmap"
import PCP from "./PCP3"
import PHP from "./PHP"
import Detail from "./Detail"
import CanvasScatter from './CanvasScatter';
import MetricCard from './MetricCard';
import BasicInfo from "./BasicInfo"
import TextCompare from "./TextCompare"
import FilterBoxPlot from "./FilterBoxPlot"
import TextHeatmap from "./TextHeatmap"
// import ColorPicker from "./removed/ColorPicker"
function App() {
  return (
  
    <div>
        <BrowserRouter>
          <Switch>
            <Route exact path='/' component={Dashboard} />
            <Route exact={true} path='/scatter' component={CanvasScatter} />
            <Route path={['/KOR', '/FRA', '/PRE']} component={Dashboard} />
            <Route path='/detail/:id/' component={Detail} />
            <Route path='/heatmap' component={FilterHeatmap} />
            <Route path='/pcp' component={PCP} />
			<Route path='/php' component={PHP} />
            <Route path='/detail/:id/' component={Detail} />
            <Route path='/basic_info' component={BasicInfo} />
            <Route path='/text_compare' component={TextCompare} />
            <Route path='/filter_box' component={FilterBoxPlot} />
            <Route path='/text_heatmap' component={TextHeatmap} />
            {/* <Route path='/ColorPicker' component={ColorPicker} /> */}

            
          </Switch>
        </BrowserRouter>
    </div>
  );
}

export default App;

